@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
.hc-developer-widget {
  min-width: 160px;
  max-width: 396px;
  max-height: 130px;
  color: #fff;
  background: #fff;
  border: 1px solid #88888820;
  border-radius: 6px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "a b c";
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding: 16px;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
  display: inline-grid;
  position: relative;
}

.hc-developer-widget--dark {
  background: #000;
  border: 1px solid #0000;
}

.hc-developer-widget--small {
  flex-wrap: wrap;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "a a"
                       "b c";
}

.hc-developer-widget--left {
  flex-wrap: wrap;
  grid-template-columns: 1fr;
  grid-template-areas: "a"
                       "b"
                       "c";
}

.hc-developer-widget__logo-container {
  height: 22px;
  width: auto;
  flex: 0 auto;
  grid-area: a;
  display: block;
}

.hc-developer-widget__logo-container--small {
  width: 100%;
  text-align: center;
  flex: auto;
}

.hc-developer-widget__logo-container--left {
  text-align: left;
}

.hc-developer-widget__logo {
  width: 110px;
}

.hc-developer-widget__star-container {
  width: auto;
  grid-area: b;
  justify-content: center;
  align-self: center;
  gap: 4px;
  display: flex;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important;
}

.hc-developer-widget__star-container--left {
  justify-content: flex-start;
}

.hc-developer-widget__star-background {
  width: 24px;
  height: 24px;
  background: #000;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hc-developer-widget__star-background--dark {
  background: #fff;
}

.hc-developer-widget__star {
  width: 16px;
  height: 16px;
}

.hc-developer-widget__rating {
  color: #0b1033;
  font-size: 14px;
  font-weight: 600;
}

.hc-developer-widget__rating--dark {
  color: #fff;
}

.hc-developer-widget__review-container {
  width: auto;
  height: 20px;
  white-space: nowrap;
  text-align: center;
  grid-area: c;
  gap: 4px;
  line-height: 20px;
  display: flex;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important;
}

.hc-developer-widget__review-container--small {
  width: 100%;
}

.hc-developer-widget__review-container--left {
  justify-content: flex-start;
}

.hc-developer-widget__separator {
  color: #0b1033 !important;
  font-size: 14px !important;
}

.hc-developer-widget__separator--dark {
  color: #fff !important;
}

.hc-developer-widget__review {
  color: #0b1033 !important;
  font-size: 14px !important;
  text-decoration: none !important;
}

.hc-developer-widget__review--dark {
  color: #fff !important;
}

